import axios from 'axios';
import http from '../plugins/http';

class WebClientApp {

  constructor(cpf, silent = false) {
    this.cpf = cpf;
    this.http = silent ? axios.create(http.appApi.defaults) : http.appApi;
  }

  //Reset de senha
  async resetConta(idregional, email) {
    const formData = new FormData();
    formData.append("cpf", this.cpf);
    formData.append("idregional", idregional.toString());
    formData.append("email", email);
    await this.http.post('/usuarios/reset.php', formData);
  }

  //Listagem de contas do CPF
  async getContas() {
    const formData = new FormData();
    formData.append('cpf', this.cpf);
    const { data: contas } = await this.http.post('/usuarios/buscar.php', formData);
    return contas;
  }

  //Cria uma nova conta
  async newConta(idregional, email, senha) {
    const formData = new FormData();
    formData.append('cpf', this.cpf);
    formData.append('idregional', idregional);
    formData.append('email', email);
    formData.append('senha', senha);
    return await this.http.post('/usuarios/cadastrar.php', formData);
  }
}

export default WebClientApp;