<template>
  <div class="card" style="width: 23rem; max-width: 95%">
    <div class="card-body">
      <header class="text-center text-warning">
        <i class="fas fa-key fa-3x"></i>
        <h5 class="mt-2">Recuperar acesso</h5>
      </header>
      <form @submit.prevent="submit" ref="form">
        <!-- regional -->
        <div class="form-group">
          <label for="ipt-regional">Regional</label>
          <select id="ipt-regional" class="form-control" required v-model="iptRegional">
            <option :value="null" disabled="disabled">SELECIONE</option>
            <option v-for="regional in regionais" :key="regional.id" :value="regional.id">
              {{regional.id}}º - {{regional.ufExtenso.toUpperCase()}}
            </option>
          </select>
        </div>
        <!-- cpf -->
        <div class="form-group">
          <label for="ipt-cpf">CPF</label>
          <the-mask id="ipt-cpf" type="tel" class="form-control" mask="###.###.###-##" required v-model="iptCpf"></the-mask>
        </div>
        <!-- email -->
        <div class="form-group">
          <label for="ipt-email">E-mail</label>
          <input id="ipt-email" type="email" class="form-control" required v-model="iptEmail">
        </div>
        <!-- botao -->
        <button ref="btnsubmit" type="submit" class="btn btn-warning btn-block" :disabled="disableSubmit">RECUPERAR</button>
        <router-link :to="{name: 'signin'}" class="btn btn-secondary btn-block">VOLTAR</router-link>
      </form>
    </div>
  </div>
</template>

<script>
  import BrazilHelper from '../../helpers/BrazilHelper';
  import CreciHelper, { regionais } from '../../helpers/CreciHelper';
  import {WebClientApp, WebClientCofeci, WebClientI2br} from '../../http';
  export default {
    name: "Recover",
    data: () => ({
      regionais,
      iptRegional: null,
      iptCpf: '',
      iptEmail: '',
      disableSubmit: false
    }),
    methods: {
      async submit() {
        this.formDisabled();
        try {
          //Validação de CPF
          if (!BrazilHelper.validarCpf(this.iptCpf)) {
            this.$toastr.warning("CPF inválido");
            throw new Error();
          }
          //Busca a conta
          const webClientApp = new WebClientApp(this.iptCpf);
          const contas = await webClientApp.getContas();
          const conta = contas.find(c => c.regional === this.iptRegional);
          if (!conta) {
            this.$toastr.error('Nenhuma conta corresponde ao CPF e região informados.');
            throw new Error();
          }

          //Separa a tratativas para clientes do ConselhoNET
          if (CreciHelper.utilizaConselhoNet(this.iptRegional)) {
            //Validação: O email digitado confere com o email na ficha do regional?
            const webClientI2br = new WebClientI2br(this.iptCpf, this.iptRegional);
            const ficha = await webClientI2br.getFicha();
            if (!ficha.contato.emails.includes(this.iptEmail)) {
              this.$toastr.warning("Este e-mail não consta na sua ficha do regional. Use outro e-mail ou peça ao CRECI que registre seu novo e-mail", "E-mail inválido");
              throw new Error();
            }
          }
          else {
            //Procura o e-mail na base de dados do COFECI
            const webClientCofeci = new WebClientCofeci(this.iptCpf, this.iptRegional);
            const emails = await webClientCofeci.buscaEmailCorretor();
            if (emails === false) {
              this.$toastr.error('O CPF não consta na base de dados nacional de corretores de imóveis. O CRECI da sua região não está integrado ao i-Corretor ou não realizaram o recadastramento nacional.');
              throw new Error();
            }
            //Validação: O email digitado na tela confere com algum dos emails recebidos do Cofeci?
            const encontrado = emails.find(email => email === this.iptEmail);
            if (!encontrado) {
              this.$toastr.warning('Este e-mail não consta na ficha nacional do corretor. Use outro e-mail ou peça ao seu regional que registre um novo e-mail no COFECI', 'E-mail não autorizado');
              throw new Error();
            }
          }
          //Realiza o reset
          await webClientApp.resetConta(this.iptRegional, this.iptEmail);
          this.$toastr.success('Enviamos uma nova senha para o e-mail', 'Senha alterada!');
          await this.$router.push({name: 'signin'});
        } catch (e) {
          // eslint-disable-next-line no-console
          console.log(e);
          this.formDisabled(false);
        }
      },
      formDisabled (desativar = true) {
        let form = this.$jquery(this.$refs.form);
        form.find('input, select').prop('disabled', desativar);
        this.disableSubmit = desativar;

        let btnsubmit = this.$jquery(this.$refs.btnsubmit);
        if (desativar) btnsubmit.html('<span class="spinner-border spinner-border-sm" role="status" aria-hidden="true"></span>');
        else btnsubmit.html('RECUPERAR');
      }
    },
    created() {
      if (this.$route.query.cpf) this.iptCpf = this.$route.query.cpf;
      if (this.$route.query.regional) this.iptRegional = parseInt(this.$route.query.regional);
    },
    beforeRouteLeave (to, from, next) {
      if (to.path.startsWith('/auth/')) {
        if (this.iptRegional) to.query.regional = this.iptRegional;
        if (this.iptCpf) to.query.cpf = this.iptCpf;
      }
      next();
    }
  }
</script>

<style scoped>
  .card-body {
    box-shadow: inset 0 4px 0 0 #ffb400;
    border-radius: .625rem;
  }
</style>