import axios from 'axios';
import http from '../plugins/http';
import CreciHelper from '../helpers/CreciHelper';
import store from '../store';

class WebClientCofeci {

  constructor(cpf, idregional, silent = false) {
    // this.cpf = cpf;
    // this.token = CreciHelper.getCofeciToken(idregional);
    this.regional = idregional;
    this.http = silent ? axios.create(http.cofeciApi.defaults) : http.cofeciApi;
    const formData = new FormData();
    formData.append("cpf", cpf);
    formData.append("token", CreciHelper.getCofeciToken(idregional));
    if (store.state.homologacao) formData.append('homologacao', 'S');
    this.formData = formData;
  }

  //Obtem os dados do corretor, NULL caso não encontre-o na base de dados.
  async buscaPessoaFJ() {
    const response = await this.http.post('/buscapessoafj.php', this.formData);
    const dadosFiltrados = response.data.find(r => r.IdCreci === this.regional);
    return dadosFiltrados ? dadosFiltrados : null;
  }

  //Obtem os dados do corretor, NULL caso ele não esteja apto a ver o cartão.
  async buscaCorretor() {
    const response = await this.http.post('/buscacorretor.php', this.formData);
    const dadosFiltrados = response.data.find(r => r.IdCreci === this.regional);
    return dadosFiltrados ? dadosFiltrados : null;
  }

  //Obtem os dados do corretor, NULL caso ele não tenha registros  de recadastramento (pela API GravarRecadastramento).
  async _situacaoFinanceiraRecadastramento() {
    const response = await this.http.post('/situacaofinanceirarecadastramento.php', this.formData);
    const dadosFiltrados = response.data.find(r => r.IdCreci === this.regional);
    return dadosFiltrados ? dadosFiltrados : null;
  }

  //Bolean. Realizou ou não o recadastramento.
  async realizouRecadastramento() {
    const response = await this._situacaoFinanceiraRecadastramento();
    return !!response;
  }

  //Array<String> || False = CPF não consta no banco do COFECI.
  async buscaEmailCorretor() {
    const {data: dados} = await this.http.post('/buscaemailcorretor.php', this.formData);
    if (dados.length === 1 && parseInt(dados[0].Erro) === 2) return false;
    return dados.filter(x => x.IdCreci === this.regional).map(y => y.EmailPagina);
  }
}

export default WebClientCofeci;